<template>
    <div class="account-select" :class=" accountaType ">
        <div class="account-wrap">
            <div class="title" v-text="title"></div>
            <div class="right">
                <div class="value-panel">
                    <p v-text="value" class="value"></p>
                    <p v-if="balance && accountaType !== 'simple'  " class="balance">
                        可用余额
                        <span class="amount">{{balance | formatNumber(2)}}</span> 元
                    </p>
                </div>
                <i class="mint-cell-allow-down"></i>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'account-select',
        props: ['title', 'value', 'balance', 'type'],
        computed: {
            accountTitle() {
                return this.title;
            },
            accountVlue() {
                return this.value;
            },
            accountBalance() {
                return this.balance;
            },
            accountaType() {
                return this.type || 'default'; // complex、default、simple
            }
        }
    };

</script>

<style lang="scss" scoped>
    .account-select {
        background-color: #fff;

        &.simple {
            .account-wrap {
                height: 100px;
            }
        }
        &.complex {
            .account-wrap {
                height: 176px;
                .right {
                    .value-panel {
                        .value {
                            font-weight: normal;
                            letter-spacing: 2.3px;
                            font-family: Farrington7B_Qiqi, "Helvetica Neue", Helvetica, STHeitiTC, sans-serif;
                        }
                        .balance {
                            color: #272727;
                            .amount {
                                color: #FF511A;
                                font-size: 15px;
                            }
                        }
                    }

                    .mint-cell-allow-down {
                        top: 8px;
                        width: 24px;
                        height: 24px;
                        @include image-set('../../assets/img/cell/list-arrow-down-black');
                    }
                }
            }
            @include image-set('../../assets/img/bank/card/cell-card');
        }
    }

    .account-wrap {
        display: block;
        margin: 0px 50px;
        padding: 30px 0;
        height: 146px;




        .title {
            float: left;
            font-size: 16px;
            color: #272727;
        }

        .right {
            position: relative;
            padding-right: 34px;
            float: right;

            .value-panel {
                display: inline-block;

                .value {
                    text-align: right;
                    font-size: 16px;
                    color: #272727;
                }

                p+p {
                    margin-top: 16px;
                }

                .balance {
                    font-size: 13px;

                    .amount {
                        margin: 0 10px;
                        color: #FF942F;
                    }
                }
            }

            .mint-cell-allow-down {
                position: absolute;
                right: 0px;
                top: 14px;

                @include transform(translate(0px, 0px));
            }
        }
    }
</style>


