<template>
    <div class=" select-payment-account-inline" :class="{ show : currentShow }"   >
        <div class="mask" @click=" close()  "></div>
        <div class="content">
            <div class="hd">
                <a href="javascript:;" class="btn-cancel" @click=" close() ">取消</a>
                <h2>{{ options.title || this.textMap[ this.type ].title }}</h2>
            </div>
            <div class="bd">
                <ul class="list" >
                    <li class="item" :class="{ checked: item.id == checkedId, 'show-bank-name': showBankName  }"  v-for="( item, index ) in accountList " :key=" item.id ">
                        <a href="javascript:;" @click="change( item, index )" >
                            <i class="icon-bank-ahrcu"></i>
                            <cite v-if="showBankName">安徽农金</cite>
                            <cite>{{ item.ACCOUNT }}</cite>
                            <span>{{ item.ALIAS || '未设置别名' }}</span>
                        </a>
                    </li>
                    <li class="item add" v-if=" showAddBtn ">
                        <a href="javascript:;" class="btn-add">{{ this.textMap[ this.type ].addBtn }}</a>
                    </li>
                    <li class="item warn" v-if=" showWarn && this.textMap[ this.type ].wran ">{{ this.textMap[ this.type ].wran }}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>


export default {
    props: [
        'options'
    ],
    data() {
        return {
            currentShow: false,
            resolve: '',
            reject: '',
            promise: '',
            textMap: {
                'payment': {
                    title: '选择付款账号',
                    addBtn: '添加本行储蓄卡',
                    wran: '付款账户仅限已添加的本行储蓄卡'
                },
                'receipt': {
                    title: '选择收款账号',
                    addBtn: '+ 添加本行储蓄卡收款',
                    wran: '+ 收款账户仅限已加挂的储蓄卡'
                },
                'credit': {
                    title: '选择信用卡',
                    addBtn: '+ 添加信用卡'
                }
            }
        };
    },
    computed: {
        type () {
            return this.options.type || 'payment';
        },
        showAddBtn () {
            return this.options.showAddBtn === undefined ? true : this.options.showAddBtn;
        },
        showWarn () {
            return this.options.showWarn === undefined ? true : this.options.showWarn;
        },
        showBankName () {
            return this.options.showBankName === undefined ? true : this.options.showBankName;
        },
        checkedId () {
            if( this.options.checked ) {
                return this.options.checked.id || -1;
            }
            return -1;
        },
        accountList (){
            if ( !this.options.accountList || !(this.options.accountList instanceof Array) ) {
                return [];
            } else {
                return this.options.accountList;
            }
        }
    },
    components: {
    },
    methods: {
        change (item, index) {
            this.currentShow = false;
            this.resolve({ item, index });
        },
        close () {
            this.currentShow = false;
            this.reject();
        },
        show () {
            this.currentShow = true;
            this.promise = new Promise((resolve, reject) => {
                this.resolve = resolve;
                this.reject = reject;
            });
            return this.promise;
        }
    }
};
</script>



<style lang="scss" scoped>

    $item-height: 120px;

    .select-payment-account-inline
    {
        position: fixed;
        top: 100%;
        left: 0px;
        right: 0px;
        bottom: 0px;
        z-index: 999;
        opacity: 0;
        overflow: hidden;
        

        @include transition(opacity 0.3s);

        &.show
        {
            top: 0px;
            opacity: 1;
            .content
            {
                top: auto;
                bottom: 0px;
            }
        }

        .mask
        {
            position: absolute;
            top: 0px;
            left: 0px;
            right: 0px;
            bottom: 0px;
            z-index: 1;
            background-color: $mask-rgba;
        }
    }

    .content
    {
        position: absolute;
        left: 0px;
        right: 0px;
        top: 100%;
        z-index: 9;
        background-color: #fff;

        .hd
        {
            position: relative;
            height: 120px;
            line-height: 120px;

            @include mini-border(bottom);
            h2
            {
                margin: 0px;
                font-size: 16px;
                font-weight: normal;
                text-align: center;
                color: #2A3342;
            }

            .btn-cancel
            {
                position: absolute;
                top: 0px;
                left: 0px;
                padding: 0px 50px;
                height: 100%;
                color: #B5B5B5;
            }
        }

        .bd
        {
        }
    }

    .list
    {
        .item
        {
            position: relative;
            @include mini-border(top);

            &:before
            {
                left: 50px;
            }
            a
            {
                display: block;
                position: relative;
                padding: 20px 0px 20px 144px;
                height: $item-height;

        
                .icon-bank-ahrcu
                {
                    left: 50px;
                }

                cite
                {
                    display: block;
                    padding-bottom: 8px;
                    color: #272727;
                    font-size: 16px;
                }
                span
                {
                    color: #B5B5B5;
                }
                
            }

            &:first-child
            {
                &:before
                {
                    content: none;
                }
            }
        }

        .item.show-bank-name
        {
            a
            {
                min-height: 170px;      
            }
        }

        .item.add
        {
            .btn-add
            {
                padding: 0px;
                padding-left: 100px;
                height: 110px;
                line-height: 110px;
                color: #308CF8;
                font-size: 16px;

                &:before
                {
                    content: '';
                    position: absolute;
                    left: 50px;
                    top: 50%;
                    width: 32px;
                    height: 32px;


                    @include bg-offset-center();

                    @include transform(translateY(-50%));

                    @include image-set('../../assets/img/bank/selector/add-card');
                }
            }
            &:before
            {
                left: 0px;
            }
        }

        .item.warn
        {
            height: 110px;;
            line-height: 110px;;
            text-align: center;
            color: #B5B5B5;
            &:before
            {
                left: 0px;
            }
        }

        .item.checked
        {
            a
            {
                &:after
                {
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: 50px;

                    width: 40px;
                    height: 40px;
                    
                    @include bg-offset-center();
                    @include transform(translateY(-50%));

                    @include image-set('../../assets/img/bank/selector/check-card');
                }
            }
        }
    }

    
</style>



