<template>
    <div class="infinite-scroll-wrapper" ref="wrapper"  >
        <div class="infinite-scroll">
            <slot></slot>
            <div class="bottom-placeholder"></div>
            <div class="bottom-loading">
                <cite>正在加载...</cite>
            </div>
        </div>
    </div>
</template>

<script >
import BScroll from 'better-scroll';
export default {
    props: {
        /**
            * 1 滚动的时候会派发scroll事件，会截流。
            * 2 滚动的时候实时派发scroll事件，不会截流。
            * 3 除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
        */
        probeType: {
            type: Number,
            default: 3
        },
        /**
            * 点击列表是否派发click事件
        */
        click: {
            type: Boolean,
            default: true
        },
        /**
            * 是否开启横向滚动
        */
        scrollX: {
            type: Boolean,
            default: false
        },
        /**
            * 是否启用回弹动画效果
        */
        bounce: {
            type: Boolean,
            default: true
        },
        /**
            * 是否派发滚动开始之前事件
        */
        listenBeforeScrollStart: {
            type: Boolean,
            default: false
        },
        /**
            * 是否派发滚动开始事件
        */
        listenScrollStart: {
            type: Boolean,
            default: true
        },
        /**
            * 是否派发滚动事件
        */
        listenScroll: {
            type: Boolean,
            default: false
        },
        listenScrollEnd: {
            type: Boolean,
            default: true
        },
        /**
            * 列表的数据
        */
        data: {
            type: Array,
            default: null
        },
        /**
            * 是否派发滚动到底部的事件，用于上拉加载
        */
        pullup: {
            type: Boolean,
            default: true
        },
        /**
            * 是否派发顶部下拉的事件，用于下拉刷新
        */
        pulldown: {
            type: Boolean,
            default: false
        },
        /**
            * 是否派发列表滚动开始的事件
        */
        beforeScroll: {
            type: Boolean,
            default: false
        },
        /**
            * 当数据更新后，刷新scroll的延时。
        */
        refreshDelay: {
            type: Number,
            default: 20
        }
    },
    data () {
        return {
            firstScrollTop: false
        };
    },
    mounted() {
        // 保证在DOM渲染完毕后初始化better-scroll
        setTimeout(() => {
            this._initScroll();
        }, 20);
    },
    methods: {
        _initScroll() {
            if (!this.$refs.wrapper) {
                return;
            }
            let _holderHeight = this.$refs.wrapper.children[0].clientHeight - this.$refs.wrapper.clientHeight;
            if( _holderHeight < 0 ) {
                this.firstScrollTop = true;
                document.querySelector('.bottom-placeholder').style.height = Math.abs( this.$flexible.convert( _holderHeight - 500 ) ) + 'px';
            }
            // better-scroll的初始化
            this.scroll = new BScroll(this.$refs.wrapper, {
                probeType: this.probeType,
                click: this.click,
                scrollX: this.scrollX,
                bounce: this.bounce
            });
            // 是否派发滚动到底部事件，用于上拉加载
            if (this.pullup) {
                this.scroll.on('scroll', () => {
                    // 滚动到底部
                    if (this.scroll.y <= (this.scroll.maxScrollY )) {
                        this.$emit('pullup');
                        if( this.scroll.maxScrollY < 0 ) {
                            document.querySelector('.bottom-loading').setAttribute('class', 'bottom-loading show');
                            document.querySelector('.bottom-placeholder').style.height = '0px';
                        }
                    }
                });
            }
            if( this.listenScrollEnd ) {
                this.scroll.on('scrollEnd', (pos) => {
                    if( this.firstScrollTop ) {
                        this.scroll.scrollTo(0, 0, 80);
                        this.firstScrollTop = false;
                    }
                });
            }
        },
        isable() {
            // 代理better-scroll的disable方法
            this.scroll && this.scroll.disable();
        },
        enable() {
            // 代理better-scroll的enable方法
            this.scroll && this.scroll.enable();
        },
        refresh() {
            // 代理better-scroll的refresh方法
            this.scroll && this.scroll.refresh();
        },
        scrollTo() {
            // 代理better-scroll的scrollTo方法
            this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
        },
        scrollToElement() {
            // 代理better-scroll的scrollToElement方法
            this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
        }
    },
    watch: {
        data () {
            setTimeout(() => {
                this.refresh();
            }, this.refreshDelay);
        }
    }
  };
</script>

<style lang="scss">
    .infinite-scroll-wrapper
    {
        position: relative;
        height: 100%;
        overflow: hidden;
        
    }
    .bottom-loading
    {
        height: 100px;
        line-height: 100px;
        text-align: center;
        color: #999;
        cite
        {
            display: none;
        }
        
        &.show
        {
            cite
            {
                display: block;
            }
        }
    }
    .bottom-placeholder
    {
        position: relative;
        overflow: hidden;
        
    }
</style>


