<template>
    <div>
        <div class="transfer-money">
            <span class="transfer-money-span">{{title}}</span>
            <div class="mint-cell mint-field is-nolabel">
                <div class="mint-cell-wrapper">
                    <div class="mint-cell-value">
                        <label class="transfer-money-label">￥</label>
                        <input class="mint-field-core" type="text"
                            :placeholder="placeholder"  
                            :value="currentValue" 
                            :totalMoney="totalMoney" 
                            @input="handleInput" 
                            @blur="handleBlur" 
                            @focus="handleFocus">
                        <hr class="transfer-hr" />
                        <div class="mint-field-clear mint-field-clear-id" style="display: none;" @click="fclear">
                            <i class="mintui mintui-field-error"></i>
                        </div>
                    </div>
                </div> 
            </div>
        </div>
        <span id="svalidate" style="padding-left: 15px;float: left;width: auto;height: 30px;line-height: 30px;color: red;display: none">{{validate}}</span>
    </div>
</template>

<script>
export default {
    props: {
        title: {
            type: String,
            default: '转账金额'
        },
        totalMoney: '',
        placeholder: {
            type: String,
            default: '请输入转账金额'
        },
        validate: {
            type: String,
            default: '转账金额不能大于原始金额'
        },
        value: {}
    },
    data() {
        return {
            money: '',
            currentValue: this.value
        };
    },
    watch: {
        value(val) {
            this.currentValue = val;
        },
        currentValue(val) {
            this.handleFocus();
            this.$emit('input', val);
        }
    },
    created() {
    },
    methods: {
        handleInput(evt) {
          this.currentValue = evt.target.value;
        },
        fclear() {
            this.currentValue = '';
            document.getElementById('svalidate').style.display = 'none';
        },
        handleFocus() {
            var c = document.getElementsByClassName('mint-field-clear-id');
            c[0] != null ? c[0].style.display = 'block' : '';
            if(this.currentValue != ''){
                c[0] != null ? c[0].style.display = 'block' : '';
            }else{
                c[0] != null ? c[0].style.display = 'none' : '';
            }
        },
        handleBlur() {
            var c = document.getElementsByClassName('mint-field-clear-id');
            c[0] != null ? c[0].style.display = 'none' : '';
            //  var totalMoney = this.totalMoney != null ? this.totalMoney.split('可用余额：') : '';
            // if(totalMoney.length > 0){
            //     totalMoney = totalMoney[1];
            // }else{
            //     totalMoney = this.totalMoney;
            // }
            var totalMoney = this.totalMoney;
            if(Number(this.currentValue) > Number(totalMoney)){
                document.getElementById('svalidate').style.display = 'block';
            }else{
                document.getElementById('svalidate').style.display = 'none';
            }
        }
    }
};

</script>

<style lang="scss" scoped>
.transfer-money {
    background: #fff;
    width: 750px;
    height: 230px;
    margin-top: 40px;
    .transfer-money-span {
        display: block;
        padding: 40px 0px 24px 50px;
        font-family: PingFangSC-Regular;
        font-size: 16px;
        color: #272727;
        letter-spacing: 0;
    }
    .transfer-money-label {
        font-family: PingFangSC-Regular;
        font-size: 20px;
        color: #272727;
        letter-spacing: 0;
        padding-right: 20px;
    }
    .transfer-hr {
        border: 0.5px solid #E4E5E6;
        position: absolute;
        left: 100px;
        top: 95px;
        right: 50px;
    }
}
</style>


