<template>
    <div class="transfer-success-detail">
        <div class="transfer-sidebar-left">
            <div class="up"></div>
            <div class="ico"></div>
            <div class="bottom"></div>
        </div>
        <!-- 三列付款人 -->
        <div class="transfer-success-items" v-if=" transferType!='2' ">
            <div class="items-img"></div>
            <div class="items-span">
                <span>{{payer}}</span>
                <span>{{payerAccount}}</span>
                <span class="span-dest">{{payerBank}}</span>
            </div>
        </div>
        <!-- 两列付款人（本人账户互转）-->
        <div class="transfer-success-items-t" v-if=" transferType=='2' ">
            <div class="items-img"></div>
            <div class="items-span">
                <span>{{payer}}</span>
                <span>{{payerAccount}}</span>
                <span class="span-dest">{{payerBank}}</span>
            </div>
        </div>
        <div class="transfer-success-items-info">
            <div class="items-info">
                <span>
                    {{payeeMoney}} 元
                    <label v-if=" transferType=='3' ">手续费 <label class="label-money">0</label> 元</label>
                </span>
                <span class="span-dest" v-if=" remark!='' && remark!=null ">附言：{{remark}}</span>
            </div>
        </div>
        <!-- 三列收款人 -->
        <div class="transfer-success-items" v-if=" transferType=='0' || transferType=='1' || transferType=='4' || transferType=='5' ">
            <div class="items-img"></div>
            <div class="items-span">
                <span>{{payee}}</span>
                <span>{{payeeAccount}}</span>
                <span class="span-dest">{{payeeBank}}</span>
            </div>
        </div>
        <!-- 两列收款人（本人账户互转、手机号转账）-->
        <div class="transfer-success-items-t" v-if=" transferType=='2' || transferType=='3' ">
            <div class="items-img"></div>
            <div class="items-span">
                <span>{{payeeAccount}}</span>
                <span class="span-dest">{{payeeBank}}</span>
            </div>
        </div>
        <div class="bottom-yz" v-if=" transferType=='0' ">
            <span>本次转账已通过云证通安全验证</span>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        transferType: '', // 0-行内云正通转账 1-行内转账 2-本人账户互转账 3-手机号转账 4-行外转账 5-农信银
        payer: '',
        payerAccount: '',
        payerBank: '',
        payee: '',
        payeeAccount: '',
        payeeBank: '',
        payeeMoney: '',
        remark: ''
    },
    data() {
        return {
        };
    },
    created() {
    },
    mounted() {
        console.log(this.remark);
    },
    methods: {
    }
};

</script>

<style lang="scss" scoped>
.transfer-success-detail {
    height: 624px;
    width: 752px;
    .transfer-sidebar-left {
        position: relative;
        float: left;
        border-right: 3px solid #46C300;
        padding-left: 123px;
        height: 348px;
        margin-top: 71px;
        .ico {
            position: absolute;
            margin-left: -20px;
            margin-top: 144px;
            width: 44px;
            height: 44px;
            background-image: url('../../assets/img/bank/transfer/icon_money@2x.png');
            background-image: image-set(url('../../assets/img/bank/transfer/icon_money@2x.png') 2x,url('../../assets/img/bank/transfer/icon_money@3x.png') 3x);
            background-repeat: no-repeat;
            background-size: 100%;
        }
        .up,.bottom {
            position: absolute;
            margin-left: -10px;
            width: 22px;
            height: 22px;
            background-color: #fff;
            border-radius: 50%;
            border: 2px solid #46C300;
        }
        .up:after,.bottom:after {
            content: '';
            position: absolute;
            width: 14px;
            height: 14px;
            background-color: #46C300;
            border-radius: 50%;
            margin: 2px 0px 0px 2px;
        }
        .bottom {
            bottom: 0px;
        }
    }
    .transfer-success-items,.transfer-success-items-t {
        margin-left: 90px;
        margin-top: 42px;
        height: 156px;
        width: 576px;
        background: #FFFFFF;
        border: 0.5px solid rgba(181,181,181,0.41);
        box-shadow: 0 2px 5px 0 rgba(232,232,232,0.50);
        border-radius: 6px;
        .items-img {
            position: absolute;
            height: 40px;
            width: 40px;
            margin: 58px 22px 58px 74px;
            background-image: url('../../assets/img/icon-bank/1@2x.png');
            background-image: image-set(url('../../assets/img/icon-bank/1@2x.png') 2x,url('../../assets/img/icon-bank/1@3x.png') 3x);
            background-repeat: no-repeat;
            background-size: 100%;
        }
        .items-span {
            position: absolute;
            margin: 20px 0px 0px 133px;
            font-size: 16px;
            color: #272727;
            span {
                display: block;
            }
            .span-dest {
                font-size: 13px;
                color: #B5B5B5;
            }
        }
    }
    .transfer-success-items-t {
        .items-span {
            margin-top: 40px;
        }
    }
    .transfer-success-items-info {
        margin-left: 90px;
        margin-top: 40px;
        height: 94px;
        width: 576px;
        .items-info {
            padding-top: 19px;
            margin-left: 74px;
            font-size: 24px;
            color: #46C300;
            span {
                display: block;
                label {
                    font-size: 13px;
                    color: #B5B5B5;
                }
            }
            .span-dest {
                font-size: 13px;
                color: #B5B5B5;
            }
            .label-money {
                color: #EE6723;
            }
        }
    }
    .bottom-yz {
        text-align: center;
        padding-top: 20px;
        font-size: 13px;
        color: #B5B5B5;
    }
}
</style>


