import * as types from '../mutation-types';

const state = {
    menuMap: {},
    menuList: [],
    menuCacheMap: {},
    menuHomeFxied: [],
    menuHomeDefault: []
};

// getters
const getters = {
    menuMap: state => state.menuMap,
    menuList: state => state.menuList,
    menuCacheMap: state => state.menuCacheMap,
    menuHomeFxied: state => state.menuHomeFxied,
    menuHomeDefault: state => state.menuHomeDefault
};

// actions
const actions = {
    setMenuMap: ({ commit }, data) => {
        commit('SET_MENU_MAP', data);
    },
    setMenuList: ({ commit }, data) => {
        commit('SET_MENU_LIST', data);
    },
    addMenuCacheMap: ({ commit }, data) => {
        commit('ADD_MENU_CACHE_MAP', data);
    },
    setMenuHomeFxied: ({ commit }, data) => {
        commit('SET_MENU_HOME_FIXED', data);
    },
    setMenuHomeDefault: ({ commit }, data) => {
        commit('SET_MENU_HOME_DEFAULT', data);
    }
};

// mutations
const mutations = {
    SET_MENU_MAP(state, data) {
        state.menuMap = data;
    },
    SET_MENU_LIST(state, data) {
        state.menuList = data;
    },
    ADD_MENU_CACHE_MAP(state, data) {
        state.menuCacheMap[ data.menuNo ] = data.menuList;
    },
    SET_MENU_HOME_FIXED(state, data) {
        state.menuHomeFxied = data;
    },
    SET_MENU_HOME_DEFAULT(state, data) {
        state.menuHomeDefault = data;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



