import store from '@/store';
import menuAuth from './menu-auth';
import pathMap from '@/router/path-map';

let app = {
    initMenu() {
        let _pathMap = {};
        for (let [key, value] of Object.entries(pathMap)) {
            _pathMap[ value ] = key;
        }
        let _menuMap = {};
        menuAuth.forEach((item)=>{
            item.PATH = _pathMap[ item.MENU_NO ];
            _menuMap[ item.MENU_NO ] = item;
        });
        let _homeFixed = menuAuth.filter((item)=>{ return item.MENU_TYPE.split('').indexOf('1') > -1 });
        let _homeDefault = menuAuth.filter((item)=>{ return item.MENU_TYPE.split('').indexOf('2') > -1 });
        _homeFixed.sort((a, b)=>{
            return a.MENU_IDX - b.MENU_IDX;
        });
        _homeDefault.sort((a, b)=>{
            return a.MENU_IDX - b.MENU_IDX;
        });
        store.dispatch('setMenuMap', Object.assign({}, _menuMap) );
        store.dispatch('setMenuList', Object.assign([], menuAuth));
        store.dispatch('setMenuHomeFxied', Object.assign([], _homeFixed));
        store.dispatch('setMenuHomeDefault', Object.assign([], _homeDefault));
    },
    init (){
        this.initMenu();
    }
};
export default app;




