<template>
    <div>
        <mt-cell v-for="(item, index) in map" :key="index" :class="{'divide': item.divide}" :title="item.title" :label="item.url"
            :is-link="link(item)">
            <img v-if="item.icon" src="../../assets/img/life/power.png" width="24" height="24">
            <span v-if="item.type === 'amount'" :style="item.style">{{dataForm[item.key] | formatNumber(2)}}</span>
            <span v-else-if="item.type === 'amount-unit'" :style="item.style">{{dataForm[item.key] | formatNumber(2)}}元</span>
            <span v-else :style="item.style">{{dataForm[item.key]}}</span>
        </mt-cell>
    </div>
</template>

<script>
    export default {
        props: ['dataCopy', 'map'],
        computed: {
            dataForm() {
                return this.dataCopy;
            },
            list() {
                return this.map;
            }
        },
        methods: {
            link(item) {
                return item.isLink || false;
            },
            setVal(item) {
                return this.dataForm[item.key];
            }
        }
    };

</script>

<style lang="scss" scoped>
    .divide {
        margin-bottom: 40px;
    }
    .divide:after {
        height: 0;
    }
</style>


