const state = {
    search: {
        checked: {},
        account: '',
        payeeName: '',
        payeeAccount: '',
        payeeBankName: '',
        money: '',
        remark: '',
        pattern: '02',
        patternName: '普通到账'
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateInternalTransferSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearInternalTransferSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            checked: {},
            account: '',
            payeeName: '',
            payeeAccount: '',
            payeeBankName: '',
            money: '',
            remark: '',
            pattern: '02',
            patternName: '普通到账'
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



