const state = {
    search: {
        checked: {},
        payeeChecked: {},
        account: '',
        accountName: '',
        accountBank: '',
        money: '',
        payeeAccount: '',
        payeeName: '',
        payeeBank: ''
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateMyAccountTransferSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearMyAccountTransferSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            checked: {},
            payeeChecked: {},
            account: '',
            accountName: '',
            accountBank: '',
            money: '',
            payeeAccount: '',
            payeeName: '',
            payeeBank: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



