import selectorPaymentAccount from './selector/payment-account.vue';
import selectorTransferAging from './selector/transfer-aging.vue';
import cell from './cell/cell.vue';
import cellMap from './cell/cell-map.vue';
import accountSelect from './cell/account-select.vue';
import cellDateZone from './cell/cell-date-zone.vue';
import cellPassword from './cell/cell-password.vue';
import bankCard from './bank/card.vue';
import dialogSmsVerify from './dialog/sms-verify.vue';
import dialogAgreement from './dialog/agreement.vue';
import submitButton from './submit-button.vue';
import betterScroll from './better-scroll.vue';
import cellTransferMoney from './bank/cell-transfer-money';
import infiniteScroll from './infinite-scroll.vue';
import tipPage from './tip-page.vue';
import transferSuccessDetail from './bank/transfer-success-detail.vue';
import transferSuccessPay from './bank/transfer-success-pay.vue';
import transferCard from './bank/transfer-card.vue';

const components = {
    install: function (Vue) {
        Vue.component('selectorPaymentAccount', selectorPaymentAccount);
        Vue.component('cell', cell);
        Vue.component('cellMap', cellMap);
        Vue.component('accountSelect', accountSelect);
        Vue.component('cellDateZone', cellDateZone);
        Vue.component('bankCard', bankCard);
        Vue.component('dialogSmsVerify', dialogSmsVerify);
        Vue.component('dialogAgreement', dialogAgreement);
        Vue.component('submitButton', submitButton);
        Vue.component('selectorTransferAging', selectorTransferAging);
        Vue.component('cellPassword', cellPassword);
        Vue.component('betterScroll', betterScroll);
        Vue.component('cellTransferMoney', cellTransferMoney);
        Vue.component('infiniteScroll', infiniteScroll);
        Vue.component('tipPage', tipPage);
        Vue.component('transferSuccessDetail', transferSuccessDetail);
        Vue.component('transferSuccessPay', transferSuccessPay);
        Vue.component('transferCard', transferCard);
    }
};
export default components;


