import Vue from 'vue';
import Vuex from 'vuex';
import * as actions from './actions';
import * as getters from './getters';
import menuAuth from './modules/menu-auth';
import testIndex from './modules/test-index';
import transactionDetail from './modules/transaction-detail';
import financeProductsRisk from './modules/finance-products/risk';
import productOrder from './modules/finance-products/transaction-product-order';
import productApply from './modules/finance-products/transaction-product-apply';
import productRedeem from './modules/finance-products/transaction-product-redeem';
import orderBack from './modules/finance-products/transaction-order-back';
import searchCustom from './modules/finance-products/search-custom';
import searchProduct from './modules/finance-products/search-product';
import searchTransaction from './modules/finance-products/search-transaction';
import life from './modules/life';
import selectorPayee from './modules/selector-payee';
import selectorBank from './modules/selector-bank';
import messageCenter from './modules/message-center';
import deviceRevocation from './modules/device-revocation';
import selectorBankCity from './modules/selector-bank-city';
import selectorBankBranch from './modules/selector-bank-branch';
import accountOpening from './modules/account-opening';
import accountLiftLevel from './modules/account-lift-level';
import relationshipChange from './modules/relationship-change';
import accountCancellation from './modules/account-cancellation';
import live2keepAll from './modules/live-keep/live2keep-all';
import live2keepLive from './modules/live-keep/live2keep-live';
import live2keepKeep from './modules/live-keep/live2keep-keep';
import keep2live from './modules/live-keep/keep2live';
import keepSearch from './modules/live-keep/keep-search';
import largeDepositReplace from './modules/large-deposit/replace';
import largeDepositBuy from './modules/large-deposit/buy';
import largeDepositDraw from './modules/large-deposit/draw';
import largeDepositCSearch from './modules/large-deposit/csearch';
import internalTransfer from './modules/internal-transfer';
import twoDimensionCode from './modules/two-dimension-code';
import externalTransfer from './modules/external-transfer';
import agriculturalCreditTransfer from './modules/agricultural-credit-transfer';
import myAccountTransfer from './modules/my-account-transfer';



Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

export default new Vuex.Store({
    actions,
    getters,
    modules: {
        menuAuth,
        testIndex,
        life,
        transactionDetail,
        financeProductsRisk,
        productOrder,
        productApply,
        productRedeem,
        orderBack,
        searchCustom,
        searchProduct,
        searchTransaction,
        selectorPayee,
        selectorBank,
        messageCenter,
        deviceRevocation,
        selectorBankCity,
        selectorBankBranch,
        accountOpening,
        accountLiftLevel,
        relationshipChange,
        accountCancellation,
        live2keepAll,
        live2keepLive,
        live2keepKeep,
        keep2live,
        keepSearch,
        largeDepositReplace,
        largeDepositBuy,
        largeDepositDraw,
        largeDepositCSearch,
        internalTransfer,
        twoDimensionCode,
        externalTransfer,
        agriculturalCreditTransfer,
        myAccountTransfer
    },
    strict: debug
});



