const state = {
    search: {
        payeeName: '',
        payeeAccount: '',
        payeeBankName: '',
        bankName: '',
        bankCity: '',
        bankBranch: '',
        account: '',
        showMoney: '',
        money: '',
        remark: '',
        pattern: '02',
        patternName: '普通到账'
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateAgriculturalCreditTransferSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearAgriculturalCreditTransferSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            payeeName: '',
            payeeAccount: '',
            payeeBankName: '',
            bankName: '',
            bankCity: '',
            bankBranch: '',
            account: '',
            showMoney: '',
            money: '',
            remark: '',
            pattern: '02',
            patternName: '普通到账'
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



