<template>
    <div class="transfer-card">
        <div class="transfer-card-items" @click="bankCardClick">
            <div class="items-img"></div>
            <div class="items-span">
                <span>{{payee}}</span>
                <span>{{payeeAccount}}</span>
                <span class="span-dest">{{payeeBank}}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        payee: '',
        payeeAccount: '',
        payeeBank: ''
    },
    data() {
        return {};
    },
    created() {
    },
    methods: {
        bankCardClick ( item ) {
            this.$emit('transfer-card-click', item);
        }
    }
};

</script>

<style lang="scss" scoped>
.transfer-card {
    height: 174px;
    width: 750px;
    border: 0.5px solid rgba(181,181,181,0.41);
    border-top: 0px;
    .transfer-card-items {
        width: 100%;
        height: 100%;
        .items-img {
            position: absolute;
            height: 64px;
            width: 64px;
            margin: 54px 30px 56px 50px;
            background-image: url('../../assets/img/icon-bank/1@2x.png');
            background-image: image-set(url('../../assets/img/icon-bank/1@2x.png') 2x,url('../../assets/img/icon-bank/1@3x.png') 3x);
            background-repeat: no-repeat;
            background-size: 100%;
        }
        .items-span {
            position: absolute;
            margin: 20px 0px 0px 133px;
            font-size: 16px;
            color: #272727;
            span {
                display: block;
                height: 44px;
            }
            .span-dest {
                font-size: 13px;
                color: #B5B5B5;
            }
        }
    }
}
</style>


