const NATIVE_NAME = 'HostAPP';
const JSBRIDGE_NAME = 'JSBridge';
const CALLBACK_NAME = 'callbackId';

const JSBridge = {
    callbackList: {},
    callbackId: function(){
        return ((+new Date() * (Math.random() + 1 ) + (1 << Math.ceil(Math.random() * 10 + 20)))).toFixed();
    },
    call: function(data) {
        data = data || {};
        let _method = data.method;
        let _params = JSON.stringify( data.params || null );
        let _callback = data.callback;
        let _callbackId = this.callbackId();
        if( !_method ) {
            console.log( 'method is not string' );
            return;
        }
        if( _callback && typeof _callback !== 'function' ) {
            console.log( 'callback is not function' );
            return;
        }
        if( _params === '{}' || _params === 'null' ) {
            _params = '';
        }
        console.log(`method: ${_method}, params: ${JSON.stringify(_params)}, callback: ${_callback}, callbackId: ${_callbackId}`);
        this.callbackList[ _callbackId ] = _callback;
        let _url = `${NATIVE_NAME}://bridge:${_callbackId}/${_method}?${_params && JSON.stringify(_params)}`;
        console.log(_url);
        if( window[NATIVE_NAME] ) {
            try {
                 window.prompt(_url, "");
            } catch (ex) {
                console.log( ex );
            }
        } else {
            console.log( 'no native, no method ...' );
        }
    },
    /*
    callbackId：回调 id
    status：操作状态
    params：回调参数
    keepCallback: 是否保持callback，主要用于密码键盘
    */
    callback: function( callbackId, status, params, keepCallback ){
        try {
            let _callback = this.callbackList[ callbackId ];
            console.log(`callbackId: ${callbackId}, params: ${JSON.stringify(params)}, callback: ${_callback}`);
            if( !_callback ) {
                console.log( 'callback is null' );
                return;
            }
            _callback && _callback( params );
            if( keepCallback !== true ) {
                delete this.callbackList[ callbackId ];
            }
        } catch( ex ) {
            console.log(`callbackId: ${callbackId}, params: ${JSON.stringify(params)}, ex: ${ex}`);
            throw ex;
        }
    }
};
window[JSBRIDGE_NAME] = JSBridge;
export default JSBridge;


