<template>
    <div class="bank-card">
        <ul class="card-list">
            <li class="item" :class="{'item-loss': item.loss}" v-for="( item, index ) in bankCardList">
                <a href="javascript:;" @click="bankCardClick( item )" :class="{'credit': cardType === 'credit'}">
                    <div class="attr">
                        <i class="logo"></i>
                        <span class="bank">{{ item.bankName }}</span>
                        <span class="type" :class="{'credit-type': cardType === 'credit'}">{{ item.type }}</span>
                    </div>
                    <div class="number">
                        <span>6231 2344 0020 3588 342</span>
                    </div>
                    <div v-show="item.alias" class="alias-contain">
                        <div class="alias">
                            <div class="line line-left"></div>
                            <div class="alias-msg">{{ item.alias }}</div>
                            <div class="line line-right"></div>
                        </div> 
                    </div>
                    <div v-show="item.isDefault" class="default">
                        <div class="msg">主账户</div>
                    </div>
                </a>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: {
        cardType: '',
        bankCardList: {
            type: Array,
            default: []
        }
    },
    data() {
        return {};
    },
    created() {
    },
    methods: {
        bankCardClick ( item ) {
            this.$emit('bank-card-click', item);
        }
    }
};

</script>

<style lang="scss" scoped>

    .bank-card
    {
        .card-list
        {
            .item
            {
                a
                {
                    position: relative;
                    display: block;
                    padding: 30px;
                    height: 238px;
                    @include border-radius(8px);
                    @include image-set('../../assets/img/bank/card/bank');
                }
                .credit
                {
                    @include image-set('../../assets/img/bank/card/credit');
                }

                .attr
                {
                    display: flex;
                    align-items: center;
                    position: relative;
                    padding-left: 56px;
                    .logo
                    {
                        position: absolute;
                        display: block;
                        top: 3px;
                        left: 0;
                        width: 36px;
                        height: 36px;
                        @include image-set('../../assets/img/bank/card/logo');
                    }

                    .bank
                    {
                        display: inline-block;
                        height: 42px;
                        font-size: 15px;
                        color: #333333;
                    }
                    .type
                    {
                        display: inline-block;
                        margin-left: 20px;
                        width: 110px;
                        height: 36px;
                        font-size: 12px;
                        color: #B5B6BE;
                        text-align: center;
                        line-height: 36px;
                        @include image-set('../../assets/img/bank/card/label');
                    }
                    .credit-type
                    {
                        color: #e5c375;
                    }
                }
                .number
                {
                    margin-top: 32px;
                    text-align: center;
                    font-size: 15px;
                    color: #272727;
                    letter-spacing: 4.28px;
                }
                .alias-contain
                {
                    position: relative;
                    margin: 24px 90px 0;
                    height: 42px;
                    overflow: hidden;
                }
                .alias
                {
                    position: absolute;
                    left: 0;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    .alias-msg
                    {
                        padding: 0 20px;
                        color: #989a9c;
                    }
                    .line
                    {
                        position: relative;
                        width: 10px;
                        height: 10px;
                        background-color: rgba(166, 168, 176, 0.5);
                        border-radius: 50%;
                    }
                    .line:after
                    {
                        content: '';
                        position: absolute;
                        top: 3.8px;
                        display: block;
                        width: 200px;
                        border-top: 1px solid rgba(166, 168, 176, 0.5);
                    }
                    .line-left:after
                    {
                        right: 0;
                    }
                    .line-right:after
                    {
                        left: 0;
                    }
                }
                .default
                {
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 84px;
                    height: 84px;
                    @include image-set('../../assets/img/bank/card/default');
                    .msg
                    {
                        position: absolute;
                        top: 24%;
                        left: 12%;
                        width: 100px;
                        color: #fff;
                        font-size: 10px;
                        transform: rotate(45deg) scale(0.8);
                    }
                }
            }
            .item + .item
            {
                margin-top: 30px;
            }
            .item-loss
            {
                a
                {
                    @include image-set('../../assets/img/bank/card/not-active');
                }
                .attr
                {
                    .logo
                    {
                        @include image-set('../../assets/img/bank/card/not-active-logo');
                    }
                    .bank{
                        color: #999999;
                    }
                }
                .number
                {
                    color: #999999;
                }
                .alias
                {
                    .alias-msg
                    {
                        color: #999999;
                    }
                }
            }
        }
    }
   
</style>


