const state = {
    search: {
        payeeName: '',
        payeeAccount: '',
        payeeBankName: '',
        account: '',
        showMoney: '',
        money: '',
        remark: '',
        pattern: '',
        patternName: '普通到账'
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateTwoDimensionCodeSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearTwoDimensionCodeSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            payeeName: '',
            payeeAccount: '',
            payeeBankName: '',
            account: '',
            showMoney: '',
            money: '',
            remark: '',
            pattern: '',
            patternName: '普通到账'
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



