<template>
    <div class="cell-date-zone">
        <label>{{ title }}</label>
        <ul class="zone-list" :class=" 'zone-list-' + zoneList.length ">
            <li class="item" v-for="( item, index ) in zoneList" :key="index">
                <a href="javascript:;" class="btn-date" :class="{ active: item.id ===  checkedId }" @click="change( item.id )">{{ item.text }}</a>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: [
        'title',
        'zone'
    ],
    data() {
        return {
            resolve: '',
            reject: '',
            promise: '',
            checkedId: -1,
            zoneList: [],
            zoneText: {
                '1W': '近1周',
                '1M': '近1个月',
                '3M': '近3个月'
            }
        };
    },
    created() {
        let _zone = (this.zone || '1W,1M,3M').replace(/\s/g, '');
        _zone.split(',').forEach((item) => {
            this.zoneList.push({
                id: item,
                text: this.zoneText[ item ]
            });
        });
        console.log(this.zoneList);
    },
    methods: {
        setDefault (begin, end) {
            var now = new Date().format('yyyy/MM/dd');
            if( now !== end ){
                this.change( this.zoneList[0].id );
                return;
            }
            if( begin === this._getDateZoneWeek() ){
                this.checkedId = '1W';
            }
            if( begin === this._getDateZoneMonth() ){
                this.checkedId = '1M';
            }
            if( begin === this._getDateZone3Month() ){
                this.checkedId = '3M';
            }
        },
        change ( id ) {
            this.checkedId = id;
            var begin = 0;
            var end = new Date().format('yyyy/MM/dd');

            if ( id === '1W' ) {
                begin = this._getDateZoneWeek();
            } else if ( id === '1M' ){
                begin = this._getDateZoneMonth();
            } else if ( id === '3M' ){
                begin = this._getDateZone3Month();
            }
            console.log('begin：' + new Date(begin).format('yyyy/MM/dd') + ' -  end：' + new Date(end).format('yyyy/MM/dd') );
            this.$emit('date-zone-change', begin, end );
        },
        _getDateZoneWeek (){
            var now = new Date();
            var day = now.getDate();
            return new Date( now.setDate( day - 7 ) ).format('yyyy/MM/dd');
        },
        _getDateZoneMonth (){
            var now = new Date();
            var day = now.getDate();
            return new Date( now.setDate( day - 30 ) ).format('yyyy/MM/dd');
        },
        _getDateZone3Month (){
            var now = new Date();
            var month = now.getMonth();
            return new Date( now.setMonth( month - 3 ) ).format('yyyy/MM/dd');
        }
    }
};

</script>

<style lang="scss" scoped>

    .cell-date-zone
    {
        position: relative;
        padding: 30px 50px;
        background-color: #fff;

        @include mini-border(bottom, #d9d9d9, after);
        @include mini-border(top, #d9d9d9, before);

        label
        {
            font-size: 16px;
            color: #666666;
        }
    }

    .zone-list
    {
        padding-top: 20px;
        display: table;
        width: 100%;
        .item
        {
            display: table-cell;
            width: 400px;
            padding: 0px 20px;
            margin-left: 40px;
            text-align: center;

            .btn-date
            {
                position: relative;
                display: block;
                width: 100%;
                height: 70px;
                line-height: 70px;
                font-size: 15px;
                color: #666;
                background: #fff;
                // border: solid 1px #999999;
                @include border-radius(6px);

                @include mini-border(all, #C6C5C7, before, 6px);

                &.active
                {
                    color: #FF511A;
                    background: #fff;
                    @include mini-border(all, #FF511A, before, 6px);
                }
            }
        }
        .item:first-child
        {
            padding-left: 0px;
            padding-right: 20px;
        }
        .item:last-child
        {
            padding-right: 0px;
            padding-left: 20px;
        }
    }
</style>


