import * as types from '../mutation-types';

const state = {
    life: {
        Power: false,
        Water: false,
        Gas: false,
        Heat: false,
        Television: false
    },
    historyList: [
        {
            id: '01',
            area: "03",
            companyName: '蚌埠电力公司3',
            company: "033",
            href: "LifePowerInfo",
            item: "电费",
            name: "王小锤",
            money: '123.45',
            no: "5654654"
        }, {
            id: '02',
            area: "03",
            company: "032",
            companyName: '蚌埠热力公司2',
            href: "LifeHeatInfo",
            item: "热力费",
            name: "王小锤",
            mark: '妈妈家的热力费',
            money: '234.56',
            no: "3456754"
        }] // historyList为测试数据，生产不需要，只是模拟后台数据
};

// getters
const getters = {
    history: state => state.life,   // 判断是否显示该功能模块入口
    historyList: state => state.historyList
};

// actions
const actions = {
    showHistory: ({ commit }, data) => {
        commit('HISTORY', data);
    },
    setHistory: ({ commit }, data) => {
        commit('SET_HISTORY', data);
    }
};

// mutations
const mutations = {
    HISTORY(state, data) {
        state.historyList.push(data);
    },
    SET_HISTORY(state, data) {
        state.life[data.item] = data.status;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



